.number-input-span {
	float: right;
}

.number-number-input {
	width: 80px;
	margin-right: 8px;
	margin-bottom: 4px;
	padding-left: 16px;
	padding-right: 4px;
	background: rgba(186, 204, 234, 0.15);
	border: solid 1px darkgrey;
	border-radius: 2px;
	text-align: left;
	font-weight: 600;
	padding-top: 4px;
	color: rgb(92, 92, 92);
	box-shadow: inset 1px 1px 4px  rgba(186, 204, 234, 1);
}

.number-number-input:hover {
	background: rgba(186, 204, 234, 0.65);
}

.number-number-input:focus {
	background: rgba(204, 88, 0, 0.16);
	box-shadow: none;
}

.text-container-div {
	margin-bottom: 4px;
	margin-top: 4px;
	clear: both;
}

.text-inline-input {
	text-align: left;
	display: inline;
	background: rgba(186, 204, 234, 0.15);
	font-weight: 600;
	color: rgb(92, 92, 92);
	box-shadow: inset 1px 1px 4px  rgba(186, 204, 234, 1);
	border: solid 1px darkgrey;
	border-radius: 2px;
	padding-left: 3px;
	padding-right: 3px;
	padding-top: 4px;
	padding-bottom: 4px;
	min-width: 120px;
}

.text-input {
	margin-right: 8px;
	padding-left: 8px;
	padding-right: 4px;
	border: solid 1px darkgrey;
	background: rgba(186, 204, 234, 0.15);
	font-weight: 600;
	padding-top: 4px;
	padding-bottom: 4px;
	color: rgb(92, 92, 92);
	box-shadow: inset 1px 1px 4px  rgba(186, 204, 234, 1);
	min-width: 120px;
	text-align: left;
	float: right;
}

.text-inline-input:focus {
	background: rgba(204, 88, 0, 0.16);
	box-shadow: none;
}

.text-input:focus {
	background: rgba(204, 88, 0, 0.16);
	box-shadow: none;
}

.insert-line-button {
	position: absolute;
	right: 2%;
	top: 52px;
	z-index: 2;
}

.formula-transformer-body .dropdown-menu {
	height: 300px;
	overflow-y: scroll;
}

.checkbox-legend {
	margin-left: 12px;
	cursor: pointer;
}

.checkbox-legend:hover {
	color: #2e4468;
}

.checkbox-input-div {
	margin-top: 8px;
	margin-left: 8px;
	margin-bottom: 8px;
	width: -webkit-max-content;
	width: max-content;
}

.checkbox-input {
	vertical-align: middle;
	width: 24px;
	height: 24px;
}

.transformer-table-input {
	width: 100%;
}

